'use client';

import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { 
  Home, 
  Calendar, 
  Users, 
  FileText, 
  Settings,
  ChevronRight
} from 'lucide-react';

const navigationItems = [
  { icon: Home, label: 'Home', href: '/doctor' },
  { icon: Calendar, label: 'Appointments', href: '/doctor/appointments' },
  { icon: Users, label: 'Patients', href: '/doctor/patients' },
  { icon: FileText, label: 'Prescriptions', href: '/doctor/prescriptions' },
  { icon: Settings, label: 'Settings', href: '/doctor/settings' },
];

export default function Sidebar() {
  const pathname = usePathname();

  return (
    <aside className="fixed left-0 top-0 h-full w-20 bg-[#1f3c88] flex flex-col items-center py-6 z-50">
      {/* Logo */}
      <div className="mb-8">
        <div className="w-12 h-12 bg-[#3b82f6] rounded-lg flex items-center justify-center">
          <span className="text-white font-bold text-lg">FN</span>
        </div>
      </div>

      {/* Navigation Items */}
      <nav className="flex-1 flex flex-col gap-4 w-full px-3">
        {navigationItems.map((item) => {
          const Icon = item.icon;
          const isActive = pathname === item.href;
          
          return (
            <Link
              key={item.href}
              href={item.href}
              className={`
                w-full h-12 flex items-center justify-center rounded-lg transition-colors
                ${isActive 
                  ? 'bg-[#3b82f6] text-white' 
                  : 'text-white/70 hover:bg-white/10 hover:text-white'
                }
              `}
              title={item.label}
            >
              <Icon size={24} />
            </Link>
          );
        })}
      </nav>

      {/* Expand/Collapse Arrow */}
      <div className="mt-auto mb-4">
        <button className="text-white/70 hover:text-white transition-colors">
          <ChevronRight size={20} />
        </button>
      </div>
    </aside>
  );
}

